
'use client';

import { useState, useEffect, type RefObject } from 'react';

interface UseInViewOptions {
  root?: Element | null;
  rootMargin?: string;
  threshold?: number | number[];
  once?: boolean;
}

export function useInView(
  ref: RefObject<Element>,
  options: UseInViewOptions = {}
): boolean {
  const [isInView, setIsInView] = useState(false);
  const { root = null, rootMargin = '0px', threshold = 0.1, once = false } = options;

  useEffect(() => {
    const element = ref.current;
    if (!element) {
        // If the ref isn't attached, we're not in view
        setIsInView(false);
        return;
    }

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
            setIsInView(true);
            if (once) {
                observer.unobserve(element);
            }
        } else {
            // If we don't want to re-animate, we do nothing when it goes out of view
            if (!once) {
                setIsInView(false);
            }
        }
      },
      { root, rootMargin, threshold }
    );

    observer.observe(element);

    return () => {
        if (element) {
            observer.unobserve(element);
        }
    };
  }, [ref, root, rootMargin, threshold, once]);

  return isInView;
}
